'use client'
import { useState } from "react";
import { createUserWithEmailAndPassword } from "firebase/auth";
import { auth } from "@/firebase";
import { InputWithLabel } from "@/components/TableInputs/tableInputs";
import ConfirmButtons from "@/components/ConfirmButtons/ConfirmButtons";

export default function DatabasePage() {
  const [menuHidden, setMenuHidden] = useState(false);
  const [newUser, setNewUser] = useState({ user: "", email: "", password: "", role: "" });
  const [error, setError] = useState<string>("");

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { id, value } = e.target;
    setNewUser((prevUser) => ({ ...prevUser, [id]: value }));
  };

  const handleSubmit = async () => {
    if (!newUser.user || !newUser.email || !newUser.password || !newUser.role) {
      setError("Por favor, complete todos los campos.");
      return;
    }

    try {
      // Crear usuario con Firebase Authentication
      const userCredential = await createUserWithEmailAndPassword(auth, newUser.email, newUser.password);

      // Extraer usuario de Firebase
      const user = userCredential.user;

      // Si deseas ver la información del usuario en Firebase
      console.log("Usuario registrado en Firebase:", user);

      alert("Usuario registrado exitosamente en Firebase");

      // Limpiar formulario después de registro
      setNewUser({ user: "", email: "", password: "", role: "" });
    } catch (error: any) {
      setError(`Error al registrar el usuario: ${error.message}`);
    }
  };

  return (
    <>
      <div className="bg-white shadow-xl rounded-xl p-6 space-y-4">
        <h1 className="text-3xl font-bold text-blue-850">Base de datos</h1>

        {error && <div className="text-red-600">{error}</div>}

        <h1 className="text-1xl font-bold text-gray-600 text-center">Registrar nuevo usuario</h1>

        <div className="mt-4 flex justify-center">
          <InputWithLabel
            label="Usuario"
            type="text"
            id="user"
            value={newUser.user}
            onChange={handleInputChange}
            placeholder="Nombre de usuario"
          />
          <InputWithLabel
            label="Correo electrónico"
            type="email"
            id="email"
            value={newUser.email}
            onChange={handleInputChange}
            placeholder="Email"
          />
          <InputWithLabel
            label="Contraseña"
            type="password"
            id="password"
            value={newUser.password}
            onChange={handleInputChange}
            placeholder="Contraseña"
          />
          <InputWithLabel
            label="Rol"
            type="text"
            id="role"
            value={newUser.role}
            onChange={handleInputChange}
            placeholder="Rol"
          />
        </div>

        <div className="mt-4 flex justify-center">
          <ConfirmButtons variant="registerNewUser" onClick={handleSubmit} />
        </div>
      </div>
    </>
  );
}
